import { useNavigate } from 'react-router-dom';
import { ModalProps } from '../utils/types';
import { AttendanceStatus } from '../utils/constant';

const statusMessageMap: Record<AttendanceStatus, string> = {
  [AttendanceStatus.Wallet_Load_Failed]: '    .',
  [AttendanceStatus.Attendance_Success]: ' !',
  [AttendanceStatus.Attendance_Failed]: '   .',
  [AttendanceStatus.Daily_Limit]: '   .',
};

const Modal = ({ status, onClose, isOpen }: ModalProps) => {
  const navigate = useNavigate();

  if (!isOpen) return null;

  const message =
    statusMessageMap[status as AttendanceStatus] ??
    '    .';

  const handleClose = () => {
    switch (status) {
      case AttendanceStatus.Wallet_Load_Failed:
        navigate('/wallet');
        break;

      // Todo:     
    }

    onClose();
  };

  return (
    <div
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: 'rgba(0,0,0,0.5)',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        zIndex: 1000,
      }}
    >
      <div
        style={{
          background: 'white',
          padding: '30px',
          borderRadius: '8px',
          minWidth: '300px',
          textAlign: 'center',
        }}
      >
        <h3>{message}</h3>
        <button
          onClick={handleClose}
          style={{
            marginTop: '20px',
            padding: '8px 16px',
            background: '#333',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer',
          }}
        >
          
        </button>
      </div>
    </div>
  );
};

export default Modal;
